import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { CommonModule } from '@angular/common';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { CreateMaintenanceRoutes } from './create-maintenance.routing';
import { CreateMaintenanceComponent } from './create-maintenance.component';
import { MaterialModule } from '../app.module';
import { NguiAutoCompleteModule } from '@ngui/auto-complete';
import { Ng2AutoCompleteModule } from 'ng2-auto-complete';
import { MdModule } from '../md/md.module';
import { FieldErrorDisplayModule } from '../field-error-display/field-error-display.module';
import { FileSelectDirective, FileDropDirective, FileUploadModule } from 'ng2-file-upload';
import { Md2DatepickerModule, MdNativeDateModule } from 'md2';

@NgModule({
    imports: [
        CommonModule,
        RouterModule.forChild(CreateMaintenanceRoutes),
        FormsModule,
        ReactiveFormsModule,
        MdModule,
        MaterialModule,
        NguiAutoCompleteModule,
        Ng2AutoCompleteModule,
        FieldErrorDisplayModule,
        FileUploadModule,
        Md2DatepickerModule,
        MdNativeDateModule 
    ],
    declarations: [
        CreateMaintenanceComponent
    ]
})

export class CreateMaintenanceModule { }


