import { Component, OnInit, AfterViewInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators, FormControl } from '@angular/forms';
import { Http, Response, RequestOptions, Headers } from "@angular/http";
import { Observable } from "rxjs/Observable";
import { Router, CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot, ActivatedRoute, Params } from '@angular/router';
import "rxjs/Rx";
import { CommonService } from "../services/commonService";
import { MaintenanceService } from "../services/maintenanceService";
import swal from 'sweetalert2';
import { FileUploader } from 'ng2-file-upload';
import { PasswordValidation } from '../forms/validationforms/password-validator.component';
import { DatePipe } from '@angular/common'

declare const $: any;

@Component({
    selector: 'app-create-maintenance',
    templateUrl: './create-maintenance.component.html',
    styleUrls: ['./create-maintenance.component.scss'],
    providers: [CommonService, MaintenanceService, DatePipe]
})
export class CreateMaintenanceComponent implements OnInit, AfterViewInit {

    public mtForm = new FormGroup({
        workOrdNo: new FormControl(),
        mType: new FormControl(),
        mPartType: new FormControl(),
        description: new FormControl(),
        flmtdoc: new FormControl()
    });

    public mtComPage = new FormGroup({
        comDesc: new FormControl(),
    });

    public mtStatus: any;
    public mtQueryID: any;
    public mTypeList: any;
    public mPartTypeList: any;
    public autoWorkOrdList: any;
    public selectedWOrd: any;
    public maintenanceData: any;
    public mtDocList: any;
    public mtprimID: any;
    public userProfile: any;
    public isEditMode: any;
    public maintenanceList: any;
    public sendDateTime: any;
    public isSend: boolean;
    public autoUsers: any;
    public assUserID: any;
    public isAssignShow: boolean = false;
    public isComplateShow: boolean = false;
    public isRejectedShow: boolean = false;

    public mtAssPage = new FormGroup({
        userName: new FormControl(),
        description: new FormControl(),
        ratetype: new FormControl(),
        rate: new FormControl(),
        fromdate: new FormControl(),
        todate: new FormControl(),
    });

    constructor(
        private http: Http,
        private router: Router,
        private commonService: CommonService,
        private maintenanceService: MaintenanceService,
        private activatedRoute: ActivatedRoute,
        private formBuilder: FormBuilder,
        public datepipe: DatePipe
    ) { }

    public uploader: FileUploader = new FileUploader({ url: this.commonService.baseApiUrl + "AddMaintenanceDocument/", itemAlias: 'file' });
    public hasBaseDropZoneOver: boolean = false;
    public hasAnotherDropZoneOver: boolean = false;


    public fileOverBase(e: any): void {
        this.hasBaseDropZoneOver = e;
    }

    public fileOverAnother(e: any): void {
        this.hasAnotherDropZoneOver = e;
    }

    isFieldValid(form: FormGroup, field: string) {
        return !form.get(field).valid && form.get(field).touched;
    }

    displayFieldCss(form: FormGroup, field: string) {
        return {
            'has-error': this.isFieldValid(form, field),
            'has-feedback': this.isFieldValid(form, field)
        };
    }

    showSwal(text: string) {
        swal({
            type: 'success',
            text: text,
            buttonsStyling: false,
            confirmButtonClass: 'btn btn-success'

        }).catch(swal.noop);
    }

    ngOnInit() {
        if (JSON.parse(sessionStorage.getItem('LogEmployee')) != null) {
            this.userProfile = JSON.parse(sessionStorage.getItem('LogEmployee'));
            this.getMaintenanceType();
            this.getMaintenancePartType();
            this.getWorkOrderForMaintenance();
            this.getAutocompleteUser();

            $(document).ready(function () {
                jQuery(".md2-datepicker-arrow").hide();
                $(".cntEmpty").removeClass("is-empty");
                $('#description').keyup(function () {
                    if (this.value.length > 240) {
                        return false;
                    }
                    $("#remainingC").html("Text Remaining : " + (240 - this.value.length));
                });
            });

            this.activatedRoute.queryParams.subscribe((params: Params) => {
                if (params['mtid'] == undefined) {
                    this.mtStatus = "Draft";
                    this.isEditMode = false;
                }
                else {
                    this.mtQueryID = params['mtid'];
                    this.getMaintenanceDetails(this.mtQueryID);
                    this.getMaintenanceDocument(this.mtQueryID);
                    this.isEditMode = true;
                }
            });

            var crntUsr = JSON.parse(sessionStorage.getItem('LogEmployee'));

            this.uploader.onAfterAddingFile = (file) => { file.withCredentials = false; };
            this.uploader.onBuildItemForm = (fileItem: any, form: any) => {
                form.append('MaintenanceID', this.mtprimID > 0 ? this.mtprimID : this.mtQueryID);
                form.append('UserId', crntUsr.ID);
                form.append('WorkOrderNo', this.selectedWOrd == undefined ? this.maintenanceData.WorkOrderNo : this.selectedWOrd.WorkOrderNo);
            };
            this.uploader.onSuccessItem = (item: any, response: string, status: number, headers: any) => {
                this.getMaintenanceDocument(this.mtprimID > 0 ? this.mtprimID : this.mtQueryID);
            }
            this.uploader.onCompleteAll = () => {
                this.uploader.clearQueue();
            };

            this.mtForm = this.formBuilder.group({
                workOrdNo: ['', Validators.required],
                mType: ['', Validators.required],
                mPartType: ['', Validators.required],
                description: ['', Validators.required]
            });

            this.mtAssPage = this.formBuilder.group({
                userName: ['', Validators.required],
                description: ['', Validators.required],
                ratetype: ['', Validators.required],
                rate: ['', Validators.required],
                fromdate: ['', Validators.required],
                todate: ['', Validators.required]
            }, {
                    validator: PasswordValidation.DateValidation // your validation method
                });

            this.mtComPage = this.formBuilder.group({
                comDesc: ['', Validators.required]
            });
        }
        else {
            this.router.navigate(['pages/login']);
        }
    }

    ngAfterViewInit() {
        const breakCards = true;
        if (breakCards === true) {
            // We break the cards headers if there is too much stress on them :-)
            $('[data-header-animation="true"]').each(function () {
                const $fix_button = $(this);
                const $card = $(this).parent('.card');
                $card.find('.fix-broken-card').click(function () {
                    const $header = $(this).parent().parent().siblings('.card-header, .card-image');
                    $header.removeClass('hinge').addClass('fadeInDown');

                    $card.attr('data-count', 0);

                    setTimeout(function () {
                        $header.removeClass('fadeInDown animate');
                    }, 480);
                });

                $card.mouseenter(function () {
                    const $this = $(this);
                    const hover_count = parseInt($this.attr('data-count'), 10) + 1 || 0;
                    $this.attr('data-count', hover_count);
                    if (hover_count >= 20) {
                        $(this).children('.card-header, .card-image').addClass('hinge animated');
                    }
                });
            });
        }
    }

    getMaintenancePartType() {
        $('.loader').show();
        return this.maintenanceService.GetActiveMaintenancePartType(this.commonService.baseApiUrl).subscribe(
            data => {
                var dataMptp = JSON.stringify(data);
                var mpTpParse = JSON.parse(dataMptp);
                var mpTpList = JSON.parse(mpTpParse["_body"]);
                if (mpTpList != null) {
                    this.mPartTypeList = mpTpList;
                    $('.loader').hide();
                }
            }
        );
    }

    resetMaintenance() {
        this.mtComPage.reset();
    }

    getMaintenanceDocument(mtID: any) {
        $('.loader').show();
        return this.maintenanceService.GetMaintenanceDocument(this.commonService.baseApiUrl, mtID).subscribe(
            data => {
                var dataMptp = JSON.stringify(data);
                var mpTpParse = JSON.parse(dataMptp);
                var mpTpList = JSON.parse(mpTpParse["_body"]);
                if (mpTpList != null) {
                    this.mtDocList = mpTpList;
                    $('.loader').hide();
                }
            }
        );
    }

    getMaintenanceDetails(mtID: any) {
        localStorage.removeItem("mtAstID");
        $('.loader').show();
        return this.maintenanceService.GetMaintenanceByID(this.commonService.baseApiUrl, mtID).subscribe(
            data => {
                var dataMt = JSON.stringify(data);
                var mTParse = JSON.parse(dataMt);
                var mTList = JSON.parse(mTParse["_body"]);
                if (mTList != null) {
                    this.maintenanceData = mTList;

                    var elems = document.querySelectorAll(".label-floating");
                    if (elems != null) {
                        [].forEach.call(elems, function (el) {
                            el.classList.remove("is-empty");
                        });
                    }
                    $("#remainingC").html("Text Remaining : " + (240 - this.maintenanceData.Description.length));

                    this.mtForm.controls["workOrdNo"].setValue(this.maintenanceData.WorkOrderNo);
                    this.mtForm.controls["mType"].setValue(this.maintenanceData.MaintenanceTypeId);
                    this.mtForm.controls["mPartType"].setValue(this.maintenanceData.MaintenancePartTypeId);
                    this.mtForm.controls["description"].setValue(this.maintenanceData.Description);
                    this.mtComPage.controls["comDesc"].setValue(this.maintenanceData.CompleteDescription);
                    this.mtStatus = this.maintenanceData.Status;
                    this.getMaintenanceAssignment(mtID);
                    $('.loader').hide();
                }
            }
        );
    }

    removeMaintenanceDocument(mdId: any, mdWONo: any, mdFlName: string) {
        swal({
            title: 'Are you sure?',
            text: 'You will not be able to revert this!',
            type: 'warning',
            showCancelButton: true,
            confirmButtonClass: 'btn btn-success',
            cancelButtonClass: 'btn btn-danger',
            confirmButtonText: 'Yes, delete it!',
            buttonsStyling: false
        }).then(() => {
            $('.loader').show();
            return this.maintenanceService.RemoveMaintenanceDocument(this.commonService.baseApiUrl, mdId, mdWONo, mdFlName).subscribe(
                data => {
                    var dataMptp = JSON.stringify(data);
                    var mpTpParse = JSON.parse(dataMptp);
                    var mpTpList = JSON.parse(mpTpParse["_body"]);
                    if (mpTpList == true) {
                        this.getMaintenanceDocument(this.mtprimID > 0 ? this.mtprimID : this.mtQueryID);
                        $('.loader').hide();
                        swal({
                            title: 'Deleted!',
                            text: 'Maintenance document has been deleted.',
                            type: 'success',
                            confirmButtonClass: 'btn btn-success',
                            buttonsStyling: false
                        }).catch(swal.noop);
                    }
                }
            );
        }).catch(swal.noop);
    }

    approvedMaintenance(status: any) {
        $('.loader').show();
        var contact = {
            Status: status
        };
        this.maintenanceService.UpdateMaintenanceStatus(this.commonService.baseApiUrl, this.mtQueryID, contact).subscribe(
            data => {
                var datatSheet = JSON.stringify(data);
                var tSheetParse = JSON.parse(datatSheet);
                var tSheetList = JSON.parse(tSheetParse["_body"]);
                if (tSheetList != null) {
                    $('.loader').hide();
                    this.getMaintenanceDetails(this.mtQueryID);
                }
            });
    }

    getMaintenanceAssignment(workOrdId: any) {
        $('.loader').show();
        this.maintenanceService.GetMaintenanceAssignment(this.commonService.baseApiUrl, workOrdId).subscribe(
            data => {
                var datawAsg = JSON.stringify(data);
                var wAsgParse = JSON.parse(datawAsg);
                var wAsgList = JSON.parse(wAsgParse["_body"]);
                this.maintenanceList = null;
                if (wAsgList != null && wAsgList.length > 0) {
                    this.maintenanceList = wAsgList;
                    if (wAsgList[0].SendDateTime != null) {
                        this.sendDateTime = wAsgList[0].SendDateTime;
                    }
                    else {
                        this.sendDateTime = null;
                    }
                    $('.loader').hide();
                    this.isSend = false;
                }
                else {
                    this.sendDateTime = null;
                    this.isSend = true;
                }
            }
        );
    }

    createMaintenance(status: string) {
        $('.loader').show();
        var mtID = this.maintenanceData == undefined ? this.mtprimID : this.maintenanceData.ID;
        if (this.mtForm.valid) {
            var crntUsr = JSON.parse(sessionStorage.getItem('LogEmployee'));
            var contact = {
                ID: mtID,
                UserId: crntUsr.ID,
                WorkOrderId: this.selectedWOrd == undefined ? this.maintenanceData.WorkOrderId : this.selectedWOrd.ID,
                OldWorkOrderId: this.selectedWOrd == undefined ? this.maintenanceData.OldWorkOrderId : this.selectedWOrd.OldWorkOrderId,
                WorkOrderNo: this.selectedWOrd == undefined ? this.maintenanceData.WorkOrderNo : this.selectedWOrd.WorkOrderNo,
                Description: this.mtForm.controls["description"].value,
                MLatitude: '-27.534630',
                MLongitude: '151.952650',
                MaintenanceTypeId: this.mtForm.controls["mType"].value,
                MaintenancePartTypeId: this.mtForm.controls["mPartType"].value,
                Status: status
            };

            this.maintenanceService.CreateMaintenance(this.commonService.baseApiUrl, contact).subscribe(
                data => {
                    var dataUsers = JSON.stringify(data);
                    var userParse = JSON.parse(dataUsers);
                    var userInfo = JSON.parse(userParse["_body"]);
                    if (userInfo != -1) {
                        this.mtprimID = userInfo;
                        this.uploader.uploadAll();
                        this.mtQueryID = userInfo > 0 && this.mtQueryID == null ? userInfo : this.mtQueryID;
                        this.getMaintenanceDetails(userInfo > 0 ? userInfo : this.mtQueryID);
                        this.getMaintenanceDocument(userInfo > 0 ? userInfo : this.mtQueryID);
                        if (mtID > 0) {
                            this.showSwal("Maintenance has been updated successfully!");
                        } else {
                            this.showSwal("Maintenance has been created successfully!");
                            this.goToMaintenance();
                        }
                        if (status == 'Unapproved') {
                            this.isRejectedShow = false;
                        }
                        $('.loader').hide();
                    }
                });
        }
        else {
            $('.loader').hide();
            this.validateAllFormFields(this.mtForm);
        }
    }

    createMaintenanceAssignment() {
        $('.loader').show();
        if (this.mtAssPage.valid) {
            let fDate = this.datepipe.transform(this.mtAssPage.controls["fromdate"].value, 'yyyy/MM/dd HH:mm:ss');
            let tDate = this.datepipe.transform(this.mtAssPage.controls["todate"].value, 'yyyy/MM/dd HH:mm:ss');
            let workOrdId = this.selectedWOrd == undefined ? this.maintenanceData.WorkOrderId : this.selectedWOrd.ID;
            var contact = {
                ID: localStorage.getItem("mtAstID"),
                UserId: this.assUserID,
                WorkOrderId: workOrdId,
                Description: this.mtAssPage.controls["description"].value,
                RateType: this.mtAssPage.controls["ratetype"].value,
                Rate: this.mtAssPage.controls["rate"].value,
                FromDate: fDate,
                ToDate: tDate,
                MaintenanceStatus: "Created",
                AssignBy: this.userProfile.ID,
                MaintenanceID: this.mtQueryID
            };
            this.maintenanceService.CreateMaintenanceAssignment(this.commonService.baseApiUrl, contact).subscribe(
                data => {
                    var dataUsers = JSON.stringify(data);
                    var userParse = JSON.parse(dataUsers);
                    var userInfo = JSON.parse(userParse["_body"]);
                    $('#myModal').modal('toggle');
                    if (userInfo == "Ok") {
                        this.getMaintenanceAssignment(this.mtQueryID);
                        this.showSwal("Maintenance has been assigned.");
                    }
                    else if (userInfo == "already") {
                        this.showSwal("Maintenance already assigned.");
                    }
                    this.mtAssPage.reset();
                    $('.loader').hide();
                });
        } else {
            this.validateAllFormFields(this.mtAssPage);
        }
    }

    getMaintenanceType() {
        $('.loader').show();
        return this.maintenanceService.GetActiveMaintenanceType(this.commonService.baseApiUrl).subscribe(
            data => {
                var dataMtp = JSON.stringify(data);
                var mTpParse = JSON.parse(dataMtp);
                var mTpList = JSON.parse(mTpParse["_body"]);
                if (mTpList != null) {
                    this.mTypeList = mTpList;
                    $('.loader').hide();
                }
            }
        );
    }

    deleteMaintenanceAssignment(mtasgID: any) {
        swal({
            title: 'Are you sure?',
            text: 'You will not be able to revert this!',
            type: 'warning',
            showCancelButton: true,
            confirmButtonClass: 'btn btn-success',
            cancelButtonClass: 'btn btn-danger',
            confirmButtonText: 'Yes, delete it!',
            buttonsStyling: false
        }).then(() => {
            $('.loader').show();
            this.maintenanceService.DeleteMaintenanceAssignment(this.commonService.baseApiUrl, mtasgID).subscribe(
                data => {
                    var dataUsers = JSON.stringify(data);
                    var userParse = JSON.parse(dataUsers);
                    var userInfo = JSON.parse(userParse["_body"]);
                    if (userInfo == true) {
                        this.getMaintenanceAssignment(this.mtQueryID);
                        $('.loader').hide();
                        swal({
                            title: 'Deleted!',
                            text: 'User has been deleted.',
                            type: 'success',
                            confirmButtonClass: 'btn btn-success',
                            buttonsStyling: false
                        }).catch(swal.noop);
                    }
                });
        }).catch(swal.noop);
    }

    getWorkOrderForMaintenance() {
        $('.loader').show();
        //this.maintenanceService.GetWorkOrderForMaintenance(this.commonService.baseApiUrl, this.userProfile.ID).subscribe(
        this.maintenanceService.GetAssignWorkOrderForMaintenance(this.commonService.baseApiUrl, this.userProfile.ID).subscribe(
            data => {
                var dataUsers = JSON.stringify(data);
                var userParse = JSON.parse(dataUsers);
                var userInfo = JSON.parse(userParse["_body"]);
                if (userInfo != null) {
                    this.autoWorkOrdList = userInfo;
                    $('.loader').hide();
                }
            });
    }

    getAutocompleteUser() {
        $('.loader').show();
        this.maintenanceService.GetAssignWOUserForMaintenance(this.commonService.baseApiUrl, this.userProfile.ID).subscribe(
            data => {
                var dataUsers = JSON.stringify(data);
                var userParse = JSON.parse(dataUsers);
                var userInfo = JSON.parse(userParse["_body"]);
                this.resetWorkOrderAssignment();
                this.autoUsers = null;
                console.log(this.autoUsers);
                if (userInfo != null) {
                    this.autoUsers = userInfo;
                    $('.loader').hide();
                }
            });
    }

    viewMaintenanceAssignment(mtID: any) {
        localStorage.removeItem("mtAstID");
        for (let woa of this.maintenanceList) {
            if (mtID == woa.ID) {
                var elems = document.querySelectorAll(".label-floating");
                if (elems != null) {
                    [].forEach.call(elems, function (el) {
                        el.classList.remove("is-empty");
                    });
                }
                localStorage.setItem("mtAstID", mtID);
                this.mtAssPage.controls["description"].setValue(woa.Description);
                this.mtAssPage.controls["rate"].setValue(woa.Rate);
                this.mtAssPage.controls["ratetype"].setValue(woa.RateType);
                this.mtAssPage.controls["fromdate"].setValue(woa.FromDate);
                this.mtAssPage.controls["todate"].setValue(woa.ToDate);
                this.mtAssPage.controls["userName"].setValue(woa.UserName);
                //this.isWoStatus = woa.WorkOrderAssignmentStatus;
                this.assUserID = woa.UserId;
            }
        }
    }

    public myUserListFormatter(data: any): string {
        return data['UserName'];
    }

    public myListFormatter(data: any): string {
        return data['WorkOrderNo'];
    }

    valueChanged(newVal) {
        this.selectedWOrd = newVal;
    }

    autoValueChanged(newVal) {
        this.assUserID = newVal.ID;
    }

    validateAllFormFields(formGroup: FormGroup) {
        Object.keys(formGroup.controls).forEach(field => {
            const control = formGroup.get(field);
            if (control instanceof FormControl) {
                control.markAsTouched({ onlySelf: true });
            } else if (control instanceof FormGroup) {
                this.validateAllFormFields(control);
            }
        });
    }

    resetWorkOrderAssignment() {
        this.mtAssPage.reset();
    }

    completeMaintenance(status: any) {
        if (this.mtComPage.valid) {
            var mtID = this.maintenanceData == undefined ? this.mtprimID : this.maintenanceData.ID;
            var crntUsr = JSON.parse(sessionStorage.getItem('LogEmployee'));
            var contact = {
                ID: mtID,
                UserId: crntUsr.ID,
                WorkOrderId: this.selectedWOrd == undefined ? this.maintenanceData.WorkOrderId : this.selectedWOrd.ID,
                OldWorkOrderId: this.selectedWOrd == undefined ? this.maintenanceData.OldWorkOrderId : this.selectedWOrd.OldWorkOrderId,
                WorkOrderNo: this.selectedWOrd == undefined ? this.maintenanceData.WorkOrderNo : this.selectedWOrd.WorkOrderNo,
                Description: this.mtForm.controls["description"].value,
                CompleteDescription: this.mtComPage.controls["comDesc"].value,
                MLatitude: '-27.534630',
                MLongitude: '151.952650',
                MaintenanceTypeId: this.mtForm.controls["mType"].value,
                MaintenancePartTypeId: this.mtForm.controls["mPartType"].value,
                Status: status
            };

            swal({
                title: 'Are you sure?',
                //text: 'You will not be able to revert this!',
                type: 'warning',
                showCancelButton: true,
                confirmButtonClass: 'btn btn-success',
                cancelButtonClass: 'btn btn-danger',
                confirmButtonText: 'Yes!',
                buttonsStyling: false
            }).then(() => {
                $('.loader').show();
                this.maintenanceService.CreateMaintenance(this.commonService.baseApiUrl, contact).subscribe(
                    data => {
                        var dataUsers = JSON.stringify(data);
                        var userParse = JSON.parse(dataUsers);
                        var userInfo = JSON.parse(userParse["_body"]);
                        if (userInfo != -1) {
                            $('.loader').hide();
                            this.mtprimID = userInfo;
                            this.uploader.uploadAll();
                            this.getMaintenanceDetails(userInfo > 0 ? userInfo : this.mtQueryID);
                            this.getMaintenanceDocument(userInfo > 0 ? userInfo : this.mtQueryID);
                            swal({
                                //title: 'Changed!',
                                text: 'Maintenance has been completed',
                                type: 'success',
                                confirmButtonClass: 'btn btn-success',
                                buttonsStyling: false
                            }).then(() => {
                                this.goToMaintenance();
                            }).catch(swal.noop);

                            $('.loader').hide();
                        }
                    });
            }).catch(swal.noop);
        }
        else {
            $('.loader').hide();
            this.validateAllFormFields(this.mtComPage);
        }
    }

    goToMaintenance() {
        this.router.navigate(['maintenance']);
    }

    buttonEditClick() {
        this.isEditMode = false;
        this.isRejectedShow = true;
    }

    isAssignModeShow() {
        this.isAssignShow = true;
    }
    isComplateModeShow() {
        this.isComplateShow = true;
    }
    sendEmailWorkOrderAssignment() {
        $('.loader').show();
        if (this.maintenanceList != null) {

            this.maintenanceService.SendEmailMaintenanceAssignment(this.commonService.baseApiUrl, this.maintenanceList).subscribe(
                data => {
                    var dataUsers = JSON.stringify(data);
                    var userParse = JSON.parse(dataUsers);
                    var userInfo = JSON.parse(userParse["_body"]);
                    if (userInfo != null) {
                        this.showSwal("Work order email has been sent successfully!");
                        this.getMaintenanceAssignment(this.mtQueryID);
                        this.autoUsers = userInfo;
                        $('.loader').hide();
                    }
                });
        }
    }

}



